body, html {
  margin: 0;
  font-size: 20px;
  line-height: 2;
  font-family: 'Open Sans', sans-serif;
}

body {
  background-color: #F7F7F7;

  display: flex;
  flex-direction: column;
  justify-content: center;

  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

#chatarea {
  margin: 0 auto;
  margin-bottom: 100px;
  width: 800px;
  height: 80%;
  padding: 20px;
  overflow-y: scroll;
  background-color: #F0F0F0;
}

.Message {
  margin: 5px;
}

.MessageContainer {
  padding: 5px;
  border-radius: 5px;
  border: 1px solid black;
  width: fit-content;
  padding-left: 20px;
  padding-right: 20px;
}

.bot > .MessageContainer {
  margin-right: auto;
  background-color: #F7F7F7;

}

.user > .MessageContainer {
  margin-left: auto;
  background-color: #A0F0A0;
}

.user {
  text-align: right;
}

.choice {
  width: 100%;
  height: 40px;
  font-size: 20px;
  margin: 6px;
  border: 1px solid black;
  color: black;
  background-color: #A0F0A0;
  box-shadow: 1px 2px 5px #707070;
  
}

.choice:first-of-type {
  margin-top: 50px;
}

.choice:hover {
  background-color: #70F070;
  cursor: pointer;
}
